  font-weight: 400;
  line-height: 27px;
  margin-bottom: 38px;
}
@media (max-width: 1199px) {
  .gym span {
    margin-bottom: 33px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .gym span {
    margin-bottom: 23px;
    text-align: start;
  }
}

.gym p {
  margin: 0;
}
@media (max-width: 1199px) {
  .gym p {
    text-align: center;
  }
  .gym p:last-of-type {
    display: none;
  }
}
@media (max-width: 767px) {
  .gym p {
    text-align: start;
  }
}

.gym p:not(:last-of-type) {
  margin-bottom: 8px;
}
@media (max-width: 1199px) {
  .gym p:not(:last-of-type) {
    margin-bottom: 43px;
  }
}
@media (max-width: 767px) {
  .gym p:not(:last-of-type) {
    margin-bottom: 33px;
  }
}

.gym p > br {
  display: none;
}
@media (max-width: 767px) {
  .gym p > br {
    display: block;
  }
}

@media (max-width: 1199px) {
  .gym picture,
.gym__image {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .gym picture,
.gym__image {
    width: 100vw;
  }
}

.gym__image {
  aspect-ratio: 660/401;
}
.gym__image--tablet, .gym__image--mobile {
  display: none;
}
@media (max-width: 1199px) {
  .gym__image {
    aspect-ratio: 678/345;
  }
  .gym__image--mobile, .gym__image--desktop {
    display: none;
  }
  .gym__image--tablet {
    display: block;
  }
}
@media (max-width: 767px) {
  .gym__image {
    aspect-ratio: 320/313;
    margin-bottom: 40px;
  }
  .gym__image--desktop, .gym__image--tablet {
    display: none;
  }
  .gym__image--mobile {
    display: block;
  }
}

.gym__video {
  position: absolute;
  display: block;
  bottom: 120px;
  left: 50%;
  transform: translateX(-10vw);
  width: 364px;
  height: 228px;
}
.gym__video--nojs {
  display: none;
}
@media (max-width: 1199px) {
  .gym__video {
    width: 34%;
    height: auto;
    bottom: 100px;
    transform: translateX(-50%);
    aspect-ratio: 266/167;
  }
}
@media (max-width: 767px) {
  .gym__video {
    position: relative;
    bottom: 0;
    left: 0;
    transform: none;
    width: 100vw;
    height: auto;
    aspect-ratio: 320/167;
  }
}

.gym__video-placeholder {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-image: url("../img/content/gym-video-poster.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.gym__video-button {
  border: none;
  background: none;
  padding: 0;
  width: 100px;
  height: 100px;
  cursor: pointer;
}
@media (hover: hover) {
  .gym__video-button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
  .gym__video-button:hover:not(.focus-visible):active, .gym__video-button:hover:not(.focus-visible).focus-visible:active {
    opacity: 0.6;
  }
}
.gym__video-button.focus-visible:focus {
  opacity: 0.8;
}
.gym__video-button.focus-visible:focus:active, .gym__video-button.focus-visible:focus.focus-visible:active {
  opacity: 0.6;
}

.gym__video-player {
  width: 100%;
  height: 100%;
}

